ul, ol, li {
    list-style: none;
    margin: 0;
    padding: 0;
}
a,
a:link,
a:hover,
a:visited,
a:active {
    text-decoration: none;
    color: unset;
}
button {
    display: inline-block;
    height: 36px;
    margin: 0;
    padding: 0 15px;
    text-align: center;
    line-height: 34px;
    color: #503396;
    font-size: 15px;
    font-weight: 600;
    border-radius: 4px;
    border: 1px solid #503396;
    background-color: #fff;
    cursor: pointer;
}
.movie-grade {
    width: 23px;
    height: 23px;
    background-position: top left;
    background-repeat: no-repeat;
    background-size: 23px;
}
i.icon-set {
    width: 17px;
    height: 15px;
    overflow: hidden;
    display: inline-block;
    margin: 0 4px 0 0;
    padding: 0;
    font-size: 0;
    line-height: 0;
    vertical-align: middle;
    background-image: url(https://img.megabox.co.kr/static/pc/images/common/ico/ico-heart-toggle-gray.png);
    background-position: 0 0;
    background-repeat: no-repeat;
}
i.icon-set.icon-main {
    background-image: url(https://img.megabox.co.kr/static/pc/images/common/ico/ico-heart-toggle-main.png);
}