.top_menu {
    display: flex;
    width: 100%;
    height: 42px;
}
.top_menu li {
    display: flex;
    flex: 1;
    height: 100%;
}
.top_menu li a {
    display: flex;
    flex: 1;
    width: 100%;
    height: 100%;
    justify-content: space-around;
    align-items: center;
    color: #222;
    font-size: 1.06em;
    border: 1px solid #ebebeb;
    border-right-width: 0;
    border-bottom: 1px solid #503396;
    box-sizing: border-box;
}
.top_menu li:last-child a {
    border-right-width: 1px;
}